a {
	color: $color__link;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

.page-content,
.entry-content,
.entry-summary,
.comment-content {

	a {
		text-decoration: none;
		border-bottom: 2px solid $color__text-main;

		&:focus,
		&:hover {
			color: $color__text-main;
		}
	}
}

a.author-link {
	color: white !important;
    text-decoration: none !important;
    &:visited {
      color: $color__link-visited !important;
    }
    &:hover,
    &:focus,
    &:active {
      color: $color__link-hover !important;
    }
}


